    font-size: 53px;
    line-height: 57px;
  }
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-direction: column;
  gap: 83px;
}
@media (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    row-gap: 111px;
    column-gap: 70px;
    grid-auto-flow: row;
  }
}

.advantages__item {
  position: relative;
  text-align: left;
}
.advantages__item--eco::before {
