.button {
  font-family: "Open Sans", "PT Sans", sans-serif;
  display: block;
  box-sizing: border-box;
  padding-top: 14px;
  padding-bottom: 14px;
  background: #ffffff;
  border: 2px solid #444444;
  width: 100%;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  color: #444444;
  cursor: pointer;
  transition: 0.2s;
}
.button:hover {
  background: #444444;
  color: #ffffff;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button:focus {
  outline: none;
  border: 2px solid #63d1bb;
}

.card {
  border: 1px solid #ececec;
  min-width: 260px;
  transition: 0.2s;
}
@media (min-width: 768px) {
  .card {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 1150px) {
  .card {
    grid-template-columns: auto;
    grid-template-rows: 462px auto;
  }
}
.card:hover .card__info {
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
}
.card:active .card__info {
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
}

.card__link {
  text-decoration-line: none;
  width: 100%;
  color: #000000;
  transition: 0.2s;
}
.card__link:hover {
