.form__input[type=radio] + .form__mark {
  border-radius: 50%;
}
.form__input[type=radio]:checked + .form__mark::before {
  position: absolute;
  content: "";
  top: 13px;
  left: 13px;
  width: 23px;
  height: 23px;
  background-color: #63d1bb;
  border-radius: 50%;
}
.form__input[type=checkbox]:checked + .form__mark::after {
  position: absolute;
  content: "";
  top: 14px;
  left: 9px;
  width: 32px;
  height: 22px;
  background-image: url("../img/tick.svg");
  background-repeat: no-repeat;
}
.form__input:focus ~ .form__mark {
  border-color: #63d1bb;
}
.form__input:hover, .form__input:active, .form__input:focus {
  box-shadow: inset 0 -2px 0 #63d1bb;
